.footerBlock {
    height: 35vh;
    background-color: #968eff;
}
.footerContainer {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.elements{
    min-width: 260px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

.element{
    background-color: #4c4c59;
    width: 50px;
    height: 50px;
}